@import '~assets/styles/h3Imports';

.root {
  display: flex;
  flex-direction: column;
  margin-bottom: rem(24);
  padding: rem(40) 0;
  text-align: center;
  width: 100%;

  .icon {
    height: rem(64);
  }

  .title {
    h5 {
      margin-bottom: 0;
    }

    color: var(--gray-3);
  }

  .description {
    @include paragraph;

    color: var(--gray-3);
    display: contents;

    @media #{$post-large-up} {
      width: rem(1000);
    }

    p {
      margin: 0;
    }
    @media #{$tablet-down} {
      width: rem(720);
    }
    @media #{$small-down} {
      width: rem(343);
    }
  }

  .button {
    align-items: center;
    display: flex;
    flex-direction: column;
    padding-top: rem(28);
  }

  .back-button {
    --button-font-size: rem(16);

    font-weight: 600;
    line-height: rem(24);
  }
}
